	module.exports = {"data":{"sectionJson":{"title":"A Wedding in Washington","imageUrl":"images/ceremony.jpg","blurb":"We are humbled and thrilled that so many of our friends and family will be traveling near and far to join us in Cle Elum, WA over Memorial Day Weekend.\n\nIt makes our hearts so happy to think about our closest friends and family spending the long weekend together, sharing in our joy and supporting our love as we exchange vows and celebrate our marriage.","subSections":[{"title":"schedule","subtitle":"dates, times, places","imageUrl":"images/wedding-alter.jpg","id":"schedule","blurb":"### Golf Round Robin\n- __Date:__ Saturday, May 26, 2018\n- __Location:__ TBD\n- __Time:__ TBD\n- __Attire:__ Golf (Duh!)\n\n### WELCOME DRINKS\nWe can’t wait until Sunday to see you. Join us for a casual gathering for drinks and snacks.\n\n- __Date:__ Saturday, May 26, 2018\n- __Location:__ TBD\n- __Time:__ TBD\n- __Attire:__ Casual\n\n\n### Wedding Ceremony\n- __Date:__ Sunday, May 27, 2018\n- __Location:__ The Lookout (Suncadia)\n- __Time:__ Late Afternoon (TBD)\n- __Attire:__ Cocktail\n- __Directions__: Turn left outside of Lodge lobby area, proceed to first left turn and turn left. After fire pit, follow to Lookout, it will be on the right. Limited parking is available outside the lodge. If staying on the resort, take the free resort shuttle.\n\n### Wedding Reception\n- __Date:__ Sunday, May 27, 2018\n- __Location:__ The Amphitheater (Suncadia)\n- __Time:__ Immediately following ceremony (TBD)\n- __Attire:__ Cocktail\n\n### Post-Wedding Brunch\nCure the hangover, reminisce about fun times the night before, and fill your stomachs before hitting the road.\n\n- __Date:__ Monday, May 28, 2018\n- __Location:__ TBD\n- __Time:__ ~9am\n- __Attire:__ Casual\n\n*Check back soon for more updates!*"},{"title":"The two of us","subtitle":"all the mushy stuff","imageUrl":"images/treelog.jpg","id":"story","blurb":"We have very intentionally planned our wedding to be a celebration of our love and life together – and a gathering of those who have been and will be a part of our lives the whole way through.\n\nThis means that if you’re on the invite list, you’re among the nearest and dearest to our hearts.  So, we are not going to share with you a novel of our “story” and who we are, but below are a few things that may surprise even you!\n\n### We Have The Travel Bug\n\n__18__: The number of countries Jonathan has visited (and he tries to learn the basics of each language before visiting each country).\n\n__26__: The number of countries Carolyn has visited.\n\n__10__: The number of countries Jonathan and Carolyn have traveled to together (and counting!).\n![image](https://raw.githubusercontent.com/azoff/wedding/master/src/images/Caro_Jon_Japan.png)\n\n### Our Middle Names\n#### Jonathan Hai Azoff\n__Hai__: The Hebrew (Chai) for “Life”.\n\n#### Carolyn May Thayer\n__May__: After Carolyn’s paternal great grandmother, Constance May McMillan.\n\n### Friends First\nJonathan and Carolyn were good friends before they started dating. In fact, they initially bonded over being single. Carolyn and Jonathan would recap and plan dates with each other.\n\n![image](https://raw.githubusercontent.com/azoff/wedding/master/src/images/C%26J_color%20run%20friends.png)\n\n### The Proposal\nJonathan proposed to Carolyn at SeaTac airport. This is best kept as a story told in-person!\n\n![image](https://raw.githubusercontent.com/azoff/wedding/master/src/images/SeaTac%20Proposal.png)\n![image](https://raw.githubusercontent.com/azoff/wedding/master/src/images/SeaTac%20Proposal%202.png)"},{"title":"wedding party","subtitle":"our BFFs","imageUrl":"images/wedding-shoes.jpg","id":"party","blurb":"Coming Soon!"}]}},"pathContext":{"id":"wedding"}}

